<template>
    <div class="add-message">
        <el-table :data="tableData" border style="width: 100%">
            <el-table-column prop="name" label="二维码名称"></el-table-column>
            <el-table-column prop="content" label="关注后回复内容"></el-table-column>
            <el-table-column prop="gonghao" label="公号"></el-table-column>
            <el-table-column prop="qr_code_url" label="二维码">
                <template slot-scope="scope">
                    <img :src="scope.qr_code_url" />
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button @click="update(scope.row)" type="primary" size="small">修改</el-button>
                    <el-button @click="delete(scope.row)" type="danger" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-form label-width="80px" size="mini">
            <el-form-item label="二维码名称" prop="name">
                <el-input v-model="name" style="width:100px;"></el-input>
            </el-form-item>
            <el-form-item label="关注回复内容" prop="content">
                <el-input type="textarea" v-model="content" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="选择公号" prop="gonghao">
                <el-checkbox-group v-model="gonghao">
                    <el-checkbox v-for="(item, index) in gonghaoList" :key="index" :label="item.gonghao" name="codes">{{item.name}}</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item size="large">
                <el-button type="primary" @click="create">立即生成</el-button>
            </el-form-item>
            <el-form-item label="二维码图片">
                <img :src="qr_code_url" />
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import Axios from 'axios'
    export default {
        data() {
            return {
                name: '',
                content: '',
                gonghao: '',
                qr_code_url: '',
                gonghaoList: []
            }
        },
        mounted(){

        },
        methods: {
            create() {

            },
            getGonghaoList() {

            }
        }
    };
</script>

<style scoped>
    
</style>




