<template>
    <div class="novel-link">

        <el-form ref="form" :model="form" label-width="80px" size="mini">
            <el-form-item label="已有id">
                <span v-for="item in items" :key="item"> -- {{item.id}} </span>
            </el-form-item>
            <el-form-item label="id">
                <el-input v-model="form.id" style="width:100px;"></el-input>
            </el-form-item>
            <el-form-item label="页面标题">
                <el-input v-model="form.title" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="文章标题">
                <el-input v-model="form.headline" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="公号名称">
                <el-input v-model="form.gonghao" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="公号描述">
                <el-input v-model="form.author" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="图片" prop="imageFile">
                <el-upload :show-file-list="false" enctype="multipart/form-data" name="imageFile" action="http://test.oorggt.top/tuiguang/novel/upload" :on-success="handleAvatarSuccess">
                    <el-button size="small" type="primary">点击上传</el-button>
                    <img v-if="imgUrl" :src="imgUrl" alt="" width="200">
                </el-upload>
            </el-form-item>
            <el-form-item label="文章内容" prop="content">
                <el-input type="textarea" v-model="form.content" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="跳转链接">
                <el-input v-model="form.linkUrl" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="统计链接">
                <el-input v-model="form.statisticsUrl" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item size="large">
                <el-button type="primary" @click="create">立即创建</el-button>
                <el-button type="danger" @click="reset">重置</el-button>
            </el-form-item>

            <el-form-item label="删除id">
                <el-input v-model="form.deleteId" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item size="large">
                <el-button type="danger" @click="deleteOne">立即删除</el-button>
            </el-form-item>
        </el-form>
        <el-dialog title="提示" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
            <span>{{links}}</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button :data-clipboard-text="links" class="copyBtn" type="primary" @click="copy">复制链接</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import Axios from 'axios'
    import Clipboard from 'clipboard';
    let clipboard = new Clipboard('.copyBtn');
    export default {
        data() {
            return {
                form: {},
                imgUrl: '',
                dialogVisible: false,
                links: '',
                items: []
            };
        },
        mounted() {
            this.getId()
        },
        methods: {
            handleAvatarSuccess(res, file) {
                this.imgUrl = 'http://test.oorggt.top/images/tuiguang/' + res.filename
                // this.imgUrl = 'http://www.rrdtjj.top/images/tuiguang/' + res.filename
                this.form.avator = this.imgUrl
            },
            create() {
                Axios({
                    url: '/tuiguang/novel/add',
                    method: 'post',
                    withCredentials: true,
                    data: {
                        id: this.form.id,
                        title: this.form.title,
                        headline: this.form.headline,
                        gonghao: this.form.gonghao,
                        author: this.form.author,
                        avator: this.form.avator,
                        content: this.form.content,
                        linkUrl: this.form.linkUrl,
                        statisticsUrl: this.form.statisticsUrl
                    }
                }).then(res => {
                    this.$message({
                        type: 'success',
                        message: res.data.message
                    })
                    this.dialogVisible = true
                    this.links = res.data.links
                    this.items.push(this.form)
                    this.form = {}
                    this.imgUrl = ''
                })
            },
            reset() {
                this.form = {}
            },
            deleteOne() {
                Axios({
                    url: '/tuiguang/novel/delete_one',
                    method: 'post',
                    data: {
                        id: this.form.deleteId
                    }
                }).then(res => {
                    this.$message({
                        type: 'success',
                        message: res.data.message
                    })
                    var arr = this.items, obj = this.form
                    for(var i=0,length=arr.length; i<length; i++) {
                        if (arr[i].id == obj.id) {
                            arr.splice(i, 1)
                        }
                    }
                    this.form.deleteId = ''
                })
            },
            copy() {
                this.$message({
                    type: 'success',
                    message: '复制成功'
                })
                this.dialogVisible = false
            },
            handleClose(done) {
                this.$confirm('确认关闭？')
                .then(_ => {
                    done();
                })
                .catch(_ => {});
            },
            getId() {
                Axios({
                    url: '/tuiguang/novel/show_id',
                    method: 'get'
                }).then(res => {
                    this.items = res.data.data
                })
            }
        }
    };
</script>

<style scoped>
    
</style>




